<template>
  <Layout class="law-page">
    <Row type="flex" style="flex-wrap: nowrap; height: 100%">
      <Card title="分类" style="width: 240px; flex: 0 0 auto; height: 100%" :bordered="true" dis-hover :padding="8">
        <Button slot="extra" type="primary" size="small" @click.native="addLawCategory()">添加</Button>
        <Tree :render="renderCategory" :data="lawCategoryList" />
      </Card>
      <Layout class="law-out" style="width: 0; margin-left: 0.5rem; display: flex; flex-direction: column;">
        <Card style="flex: 1 1 auto;" dis-hover :padding="8">
          <Row slot="title" style="margin-bottom: 0.5rem">
            <Breadcrumb>
              <BreadcrumbItem style="cursor: pointer" v-for="(item, index) in breadcrumbs" :key="index" @click.native="clickBrc(item, index)">{{item.title}}</BreadcrumbItem>
            </Breadcrumb>
          </Row>
          <span slot="extra">
            <Button v-if="breadcrumbs.length === 1" type="primary" size="small" @click.native="$router.push({ name: 'LawDetail', params: { type: 'new', id: 0, category: breadcrumbs[0].path.params.categoryId } })">添加法律</Button>
            <Button v-if="breadcrumbs.length > 1" type="primary" size="small" @click.native="addLawChapter()">添加章节</Button>
            <Button v-if="breadcrumbs.length > 1" style="margin-left: 0.5rem" type="primary" size="small" @click.native="addLawItem()">添加法条</Button>
          </span>
          <router-view />
        </Card>
      </Layout>
    </Row>

    <Modal
      :mask-closable="false"
      title="编辑分类"
      v-model="addLawCategoryModal"
      :loading="true"
      @on-ok="saveCategory">
      <Form :model="lawCategoryData">
        <FormItem label="名称">
          <Input v-model="lawCategoryData.name" />
        </FormItem>
        <FormItem label="别名">
          <Input v-model="lawCategoryData.alias" />
        </FormItem>
        <FormItem label="检索索引">
          <Input :value="lawCategoryData.keywords" @on-change="keywordsChange" />
        </FormItem>
        <FormItem label="显示顺序">
          <Input v-model="lawCategoryData.sort" />
        </FormItem>
      </Form>
    </Modal>
    <Modal
      :mask-closable="false"
      title="编辑章节"
      v-model="addLawChapterModal"
      :loading="true"
      @on-ok="saveChapter">
      <Form :model="lawChapterData">
        <FormItem label="名称">
          <Input v-model="lawChapterData.title" />
        </FormItem>
      </Form>
    </Modal>
    <Modal
      :mask-closable="false"
      title="编辑法条"
      v-model="addLawItemModal"
      :loading="true"
      @on-ok="saveLawItem">
      <Form :model="lawItemData">
        <FormItem label="标题">
          <Input v-model="lawItemData.title" />
        </FormItem>
        <FormItem label="标签">
          <tagSelector v-model="lawItemData.tagInfo" />
        </FormItem>
        <FormItem label="分值">
          <Input v-model="lawItemData.score" />
        </FormItem>
        <FormItem label="排序">
          <Input v-model="lawItemData.sort" />
        </FormItem>
        <FormItem label="内容">
          <Input type="textarea" v-model="lawItemData.content" />
        </FormItem>
      </Form>
    </Modal>
  </Layout>
</template>

<script>
import {
  saveCategory,
  saveLawItem,
  getCategoryList,
  deleteCategory
} from '@/api/law'
import tagSelector from '../components/tag-selector'

const LawItemModel = {
  categoryId: null, // 非必填
  content: '',
  documentLevel: 0,
  id: null,
  keywords: '',
  lawId: null,
  level: 0,
  parentId: null,
  score: 0,
  sort: 0,
  tagInfo: [],
  title: ''
}

export default {
  name: 'law',
  components: {
    tagSelector
  },
  data () {
    return {
      addLawCategoryModal: false,
      addLawChapterModal: false,
      lawCategoryData: {
        alias: '',
        keywords: '',
        level: 0,
        name: '',
        parentId: null,
        sort: 0,
        treePath: ''
      },
      lawChapterData: this.deepCopy(LawItemModel),
      addLawItemModal: false,
      lawItemData: this.deepCopy(LawItemModel),
      lawCategoryList: [],
      currentCategory: null,
      currentLaw: null
    }
  },
  created () {
    this.getCategoryList()
    if (this.breadcrumbs.length) {
      this.$router.push(this.breadcrumbs[this.breadcrumbs.length - 1].path)
    }

    window.addEventListener('popstate', () => {
      this.$store.commit('law/navPop')
    })
  },
  watch: {
  },
  computed: {
    breadcrumbs () {
      return this.$store.state.law.breadcrumbs
    }
  },
  methods: {
    clickBrc (brc, index) {
      this.$store.commit('law/navReplace', this.breadcrumbs.slice(0, index + 1))
      this.$router.push(brc.path)
    },
    resetCategoryData () {
      this.lawCategoryData = {
        alias: '',
        keywords: '',
        level: 0,
        name: '',
        parentId: null,
        sort: 0,
        treePath: ''
      }
    },
    addLawCategory (parent = null) {
      this.resetCategoryData()
      if (parent) {
        this.lawCategoryData.parentId = parent.id
      }
      this.addLawCategoryModal = true
    },
    editCategory (data) {
      this.lawCategoryData = data
      this.addLawCategoryModal = true
    },
    saveCategory () {
      saveCategory(this.lawCategoryData).then(({ data, response }) => {
        if (response.success) {
          this.$Message.success('保存成功！')
          this.addLawCategoryModal = false
          this.getCategoryList()
        } else {
          this.$Message.error(response.msg)
        }
      })
    },
    deleteCategory (data) {
      this.$Modal.confirm({
        title: '提示',
        content: '删除之后无法恢复，请确定是否删除？',
        onCancel: () => {},
        onOk: () => {
          deleteCategory({
            id: data.id
          }).then(({ data, response }) => {
            if (response.success) {
              this.$Message.success('删除成功！')
              this.getCategoryList()
            } else {
              this.$Message.error(response.msg)
            }
          })
        }
      })
    },
    getCategoryList () { // 获取法律门类列表
      getCategoryList({}).then(({ response, data }) => {
        if (response.success) {
          this.lawCategoryList = this.makeData(data)
          console.log(this.lawCategoryList)
        } else {
          console.error(response.msg)
        }
      })
    },
    addLawChapter (parent = null) {
      this.addLawChapterModal = true
    },
    saveChapter () {
      this.lawChapterData.lawId = this.$store.state.law.currentLaw.id
      this.lawChapterData.parentId = this.$store.state.law.currentChapter || null
      this.lawChapterData.leaf = 0
      saveLawItem(this.lawChapterData).then(({ data, response }) => {
        if (response.success) {
          this.addLawChapterModal = false
          this.lawChapterData = this.deepCopy(LawItemModel)
          this.$store.commit('law/publish', 'reload-law-item')
        }
      })
    },
    addLawItem () {
      this.addLawItemModal = true
    },
    saveLawItem () {
      this.lawItemData.lawId = this.$store.state.law.currentLaw.id
      this.lawItemData.parentId = this.$store.state.law.currentChapter || null
      this.lawItemData.leaf = 1
      saveLawItem(this.lawItemData).then(({ data, response }) => {
        if (response.success) {
          this.addLawItemModal = false
          this.lawItemData = this.deepCopy(LawItemModel)
          this.$store.commit('law/publish', 'reload-law-item')
        }
      })
    },
    renderCategory (h, { root, node, data }) {
      return h(
        'span',
        [
          h(
            'Tag',
            {
              props: {
                color: 'blue'
              },
              nativeOn: {
                click: () => {
                  this.currentCategory = data
                  this.$store.commit('law/navReplace', [{ title: data.name, path: { name: 'LawList', params: { categoryId: data.id } } }])
                  this.$router.push({ name: 'LawList', params: { categoryId: data.id } })
                }
              }
            },
            data.name
          ),
          h(
            'Button',
            {
              props: {
                size: 'small',
                type: 'default',
                icon: 'md-create'
              },
              style: {
                marginLeft: '0.5rem'
              },
              nativeOn: {
                click: () => {
                  this.editCategory(data)
                }
              }
            }
          ),
          h(
            'Button',
            {
              props: {
                size: 'small',
                type: 'success',
                icon: 'md-add'
              },
              style: {
                marginLeft: '0.2rem'
              },
              nativeOn: {
                click: () => {
                  this.addLawCategory(data)
                }
              }
            }
          ),
          h(
            'Button',
            {
              props: {
                size: 'small',
                type: 'error',
                icon: 'md-trash'
              },
              style: {
                marginLeft: '0.2rem'
              },
              nativeOn: {
                click: () => {
                  this.deleteCategory(data)
                }
              }
            }
          )
        ]
      )
    },
    keywordsChange (e) {
      this.lawCategoryData.keywords = e.target.value.replace(/，/g, ',')
    },
    changeLawItemPage (page) {
      this.lawItemPagination.page = page
      this.getLawItemList()
    },
    changeLawItemPageSize (pageSize) {
      this.lawItemPagination.pageSize = pageSize
      this.getLawItemList()
    },
    makeData (list = []) {
      if (!list) return []
      const rootLevel = []
      const subLevel = []
      const categoryMap = new Map()
      // 生成map，分拣出根级数据和子集
      for (let item of list) {
        item.expand = true

        categoryMap.set(item.id, item)
        if (!item.parentId) {
          rootLevel.push(item)
        } else {
          subLevel.push(item)
        }
      }

      // 将子集数据放入父级节点的chidren中
      for (let item of subLevel) {
        const parentNode = categoryMap.get(item.parentId)
        if (!parentNode) {
          return
        }
        if (parentNode.children) {
          parentNode.children.push(item)
        } else {
          parentNode.children = [ item ]
        }
      }

      return rootLevel
    }
  }
}
</script>

<style scoped lang="stylus">
.law-page
  background none
  height 100%
  .tool-bar
    display flex
    margin-bottom 0.5rem
    .left
      flex 1 1 auto
    .right
      flex 1 0 auto
      text-align right
</style>

<style lang="stylus">
.law-page
  .ivu-card
    display flex
    flex-direction column
    .ivu-card-head
      flex 0 0 auto
    .ivu-card-body
      overflow: auto;
      flex: 1 0 auto;
      height: 0;

  .law-out
    background none
</style>
